import { ThunkResult } from '../../types/thunk';
import {
  requestCar,
  requestCarError,
  requestCars,
  requestCarsError,
  requestCarsSuccess, requestCarSuccess, uploadCar, uploadCarError, uploadCarSuccess
} from './actions';
import { CarObject, CarsAxiosConfig } from '../../types/Cars';
import { addSuccessAlert, addToErrorHandler } from '../ErrorProvider/actions';

export const fetchCars = (config: CarsAxiosConfig): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(requestCars());
    const { data } = await services.table.getEntities('car', config);
    dispatch(requestCarsSuccess(data));
  } catch (error) {
    dispatch(requestCarsError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const fetchCar = (id): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(requestCar());
    const { data } = await services.table.getEntities('car', {}, id);
    dispatch(requestCarSuccess(data.data));
  } catch (error) {
    dispatch(requestCarError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const createCar = (newCar: CarObject): ThunkResult => {
  return async (dispatch, getState, { services }) => {
    try {
      dispatch(uploadCar());
      const { data } = await services.table.postEntity('car', newCar);
      dispatch(uploadCarSuccess(data.data));
      dispatch(addSuccessAlert('Машина создана'));
    } catch (error) {
      dispatch(uploadCarError(error.response.status));
      dispatch(addToErrorHandler(error.response.status));
    }
  };
};

export const editCar = (car: CarObject, id: string): ThunkResult => {
  return async (dispatch, getState, { services }) => {
    try {
      dispatch(uploadCar());
      const { data } = await services.table.putEntity('car', car, id);
      dispatch(uploadCarSuccess(data.data));
      dispatch(addSuccessAlert('Машина сохранена'));
    } catch (error) {
      dispatch(uploadCarError(error.response.status));
      dispatch(addToErrorHandler(error.response.status));
    }
  };
};
